import * as React from "react";
import { styled } from "@mui/material/styles";
import ArrowForwardIosSharpIcon from "@mui/icons-material/ArrowForwardIosSharp";
import MuiAccordion from "@mui/material/Accordion";
import MuiAccordionSummary from "@mui/material/AccordionSummary";
import MuiAccordionDetails from "@mui/material/AccordionDetails";
import { Typography, Grid } from "@mui/material";
import closeIcon from "../../assets/close.png";
import { useAppContext } from "../../context/appContext";
const Accordion = styled((props) => (
  <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
  border: `1px solid ${theme.palette.divider}`,
  "&:not(:last-child)": {
    borderBottom: 0,
  },
  "&:before": {
    display: "none",
  },
}));

const AccordionSummary = styled((props) => (
  <MuiAccordionSummary
    expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: "0.9rem" }} />}
    {...props}
  />
))(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === "dark"
      ? "rgba(255, 255, 255, .05)"
      : "rgba(0, 0, 0, .03)",
  flexDirection: "row-reverse",
  "& .MuiAccordionSummary-expandIconWrapper.Mui-expanded": {
    transform: "rotate(90deg)",
  },
  "& .MuiAccordionSummary-content": {
    marginLeft: theme.spacing(1),
  },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
  padding: theme.spacing(2),
  borderTop: "1px solid rgba(0, 0, 0, .125)",
}));

export default function CustomizedAccordions(props) {
  const { process_action_fields, selectedProcessOperationID, selectedProcessStageID } = props;
  const [expanded, setExpanded] = React.useState("");
  const { mainTreeData, updateMainTreeData } = useAppContext();

  const handleChange = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };


  const removeProcessActionSelectedData = (data, firstLevelID, secondLevelID, selectedData) => {
    for (let i = 0; i < data.length; i++) {
      const firstLevel = data[i];
      if (firstLevel.id === firstLevelID && firstLevel.children) {
        for (let j = 0; j < firstLevel.children.length; j++) {
          const secondLevel = firstLevel.children[j];
          if (secondLevel.id === secondLevelID) {
            for (let k = 0; k < secondLevel.children.length; k++) {
              const thirdLevel = secondLevel.children[k];
              if (thirdLevel.id === selectedData.id) {
                secondLevel.children.splice(k, 1)
              }
            }
            return true;
          }
        }
      }
    }
    return false;
  };
  const arr = [
    {
      name: "Temperature",
      fields: [
        { label: "Target", id: "target" },
        { label: "UoM", id: "uom" },
        { label: "Min", id: "min" },
        { label: "Max", id: "max" },
      ],
    },
    {
      name: "Volume",
      fields: [
        { label: "Target", id: "target" },
        { label: "UoM", id: "uom" },
        { label: "Min", id: "min" },
        { label: "Max", id: "max" },
      ],
    },
    {
      name: "pH",
      fields: [
        { label: "Target", id: "target" },
        { label: "UoM", id: "uom" },
        { label: "Min", id: "min" },
        { label: "Max", id: "max" },
      ],
    },
  ];
  return (
    <>
      {process_action_fields && process_action_fields.id === selectedProcessOperationID &&
        process_action_fields
          .children.map((data) => {
            return (
              <Accordion
                key={data.id}
                expanded={expanded === data.id}
                onChange={handleChange(data.id)}
              >
                <AccordionSummary
                  aria-controls="panel1d-content"
                  id="panel1d-header"
                >
                  <Grid
                    container
                    sx={{
                      display: "flex",
                      justifyContent: "space-between",
                      width: "100%",
                    }}
                  >
                    <Grid item>
                      <Typography>{data.label}</Typography>
                    </Grid>
                    <Grid item>
                      <img
                        src={closeIcon}
                        alt="closeIcon"
                        style={{ width: "0.75rem" }}
                        onClick={() => removeProcessActionSelectedData(mainTreeData, selectedProcessStageID, selectedProcessOperationID, data)}
                      />
                    </Grid>
                  </Grid>
                </AccordionSummary>
                <AccordionDetails>
                  {data.children.map((children_data) => {
                    return (
                      <>
                        <div style={{ textAlign: "left", marginBottom: "1em" }}>
                          <Typography variant="h6" gutterBottom>
                            <b>{children_data.label}</b>
                          </Typography>
                          <div
                            style={{
                              textAlign: "left",
                              display: "flex",
                              justifyContent: "space-between",
                            }}
                          >
                            {children_data.fields.map((field_data) => {
                              return (
                                <label>
                                  {field_data.label} <input />
                                </label>
                              );
                            })}
                          </div>
                          <div>
                            <br />
                            <Typography variant="h6" gutterBottom>
                              <b>Process Parameters</b>
                            </Typography>
                            {arr.map((item) => {
                              return (
                                <>
                                  <div style={{ marginBottom: "1px" }}>
                                    <Typography gutterBottom>
                                      <b>{item.name}</b>
                                    </Typography>
                                  </div>
                                  <div
                                    style={{
                                      textAlign: "left",
                                      display: "flex",
                                      justifyContent: "space-between",
                                      marginBottom: "10px",
                                    }}
                                  >
                                    {item.fields.map((field) => {
                                      return (
                                        <>
                                          <label>{field.label}</label>
                                          <input />
                                        </>
                                      );
                                    })}
                                  </div>
                                </>
                              );
                            })}
                          </div>
                        </div>
                      </>
                    );
                  })}
                </AccordionDetails>
              </Accordion>
            );
          })}
    </>
  );
}
